<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<image v-if="models=='0'" class="returnIcon" src="../../static/return_white.png" @click="returnLastpage"></image>
		<image v-else class="returnIcon" src="../../static/return_black.png" @click="returnLastpage"></image>
		<image class="banner" src="../../static/index/exchangeBanner.png" :lazy-load="true"></image>
		<view class="content">
			<img class="exchangeTitle" src="../../static/index/exchange_title.png">
			<view class="" v-for="(item,index) in data" :key="index">
				<img :src="item.img">
				<button v-if="item.state==0" class="enough" @click="exchangeInfo(index)">点击兑换</button>
				<button v-else :disabled="true">已兑换</button>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { exchangeList, exchange } from '@/common/http.api.js';
	export default{
		data() {
			return {
				models:'1', //白天模式
				token:'',
				data:{}
			}
		},
		onLoad() {
			this.models = store.state.model
			this.token = store.state.vuex_token
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#011733',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#6ab4ff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
			exchangeList({token:this.token}).then(res=>{
				console.log(res)
				this.data = res.data.data
			}).catch(err=>{
				console.log(err)
			})
		},
		onShow() {
			
		},
		methods: {
			// 返回上级页面
			returnLastpage() {
				uni.navigateBack({
					delta:1
				})
			},
			// 兑换积分
			exchangeInfo(e) {
				console.log(e)
				exchange({token:this.token, type:e}).then(res=>{
				console.log(res)
				this.$u.toast(res.data.data)
				exchangeList({token:this.token}).then(res=>{
					console.log(res)
					this.data = res.data.data
				}).catch(err=>{
					console.log(err)
				})
			}).catch(err=>{
				console.log(err)
			})
			}
		}
	}
</script>

<style lang="scss">
	.returnIcon{
		width: 50rpx;
		height: 50rpx;
		position: fixed;
		top: 6%;
		z-index: 5;
		left: 50rpx;
	}
	.banner{
		width: 90%;
		margin: 0rpx 5%;
		margin-top: 180rpx;
		height: 500rpx;
	}
	.content{
		width: 90%;
		margin: 50rpx 5% 30rpx 5%;
		background-color: #fff;
		border-radius: 20rpx;
		padding-bottom: 30rpx;
		padding-top: 60rpx;
		position: relative;
		.exchangeTitle{
			position: absolute;
			top: -44rpx;
			left: 25%;
			width: 50%;
			height: 80rpx;
		}
		view{
			width: 90%;
			margin: 0 5%;
			margin-bottom: 30rpx;
			img{
				width: 100%;
				border-radius: 10rpx;
				border: 1px solid #78b2ff;
			}
			button{
				background-color: #d3d3d3;
				color: #fff;
				line-height: 88rpx;
				margin: 30rpx 0;
				border-radius: 100rpx;
			}
			.enough{
				background-color: #3995ea;
			}
		}
	}
	.body{
		background-image: url(../../static/index/exchangeBg.png);
		padding-bottom: 30rpx;
	}
	.bodyNight{
		background-color: #3B3B3B;
		padding-bottom: 30rpx;
		.content{
			background-color: #303030;
		}
	}
</style>